import React, { useEffect } from 'react';
// import hooka useEffect zeby mc go wykorzysta

const Home = () => {
  useEffect(() => {
    // useEffect jest odpowiedzialny za wykonanie funkcji po zaadowaniu strony
    document.title = 'Home';
    // ustawiam tytu strony na Home
  }, []);
  // [] - tablica zalenoci, jeli tablica jest pusta to funkcja useEffect wykona si tylko raz po zaadowaniu strony

  return (
    <h1 className="title">Recruitment Task</h1>
    // wywietlam tytu strony
  );
};

export default Home;
// eksportuj komponent Home
