import React, { useState } from 'react';
// importuje hooka useState eby mc go wykorzysta
import { Link } from 'react-router-dom';
// importuje link z react-router-dom
import MenuIcon from '@mui/icons-material/Menu';
// importuje ikonk menu z biblioteki material-ui
import CloseIcon from '@mui/icons-material/Close';
// importuje ikonk zamknicia z biblioteki material-ui

const Navigation = () => {
  const [isOpen, setIsOpen] = useState(false);
  // tworz zmienn isOpen i funkcj setIsOpen, pocztkowo ustawiam j na false poniewaz menu jest zamknite

  return (
    <nav className="container">
      {/* 
        dodaje semantyczny  znacznik nav, ktry bdzie zawiera nawigacj
      */}
      <ul className="wrapper">
        {/* 
          znacznik ul bdzie zawiera list elementw czyli linki do innych stron
        */}
        <li className="wrapperItem">
          {/*   
            znacznik li bdzie zawiera pojedynczy element listy 
          */}
          <Link className="navItem" to="/">
            {/* 
               Link pozwala na nawigacj pomidzy stronami, do atrybutu to="/" przypisuj ciek do strony gwnej. Link renderuje si jako znacznik a
            */}
            Home
          </Link>
        </li>
        <li>
          <Link className="navItem" to="/posts">
            {/* 
                Link pozwala na nawigacj pomidzy stronami, do atrybutu to="/posts" przypisuj ciek do strony z postami. Link renderuje si jako znacznik a
            */}
            Posts
          </Link>
        </li>
        <li>
          <Link className="navItem" to="/todos">
            {/*
                Link pozwala na nawigacj pomidzy stronami, do atrybutu to="/todos" przypisuj ciek do strony z zadaniami. Link renderuje si jako znacznik a
            */}
            Todos
          </Link>
        </li>
        <li>
          <Link className="navItem" to="/users">
            {/*
                Link pozwala na nawigacj pomidzy stronami, do atrybutu to="/users" przypisuj ciek do strony z uytkownikami. Link renderuje si jako znacznik a
            */}
            Users
          </Link>
        </li>
      </ul>
      <button className="menuIcon" onClick={() => setIsOpen((prev) => !prev)}>
        {/* 
            dodaje przycisk, ktry bdzie na kliknicie otwiera i zamyka menu. W funkcji setIsOpen przekazuj poprzedni stan zmiennej isOpen i odwracam jego warto (negacja) dziki temu menu bdzie si otwierao i zamykao
          */}
        {isOpen ? <CloseIcon /> : <MenuIcon />}
        {/* 
            jeeli isOpen bdzie true to wywietlam ikonk zamknicia, jeeli false to ikonk menu
        */}
      </button>

      {isOpen && (
        // jeeli isOpen bdzie true to wywietlam nawigacj
        <ul className="showWrapper">
          {/* 
            znacznik ul bdzie zawiera list elementw czyli linki do innych stron
          */}
          <li className="wrapperItem">
            {/*
              znacznik li bdzie zawiera pojedynczy element listy
            */}
            <Link className="showNavItem" to="/">
              {/* 
                Link pozwala na nawigacj pomidzy stronami, do atrybutu to="/" przypisuj ciek do strony gwnej. Link renderuje si jako znacznik a
              */}
              Home
            </Link>
          </li>
          <li>
            {/*
              znacznik li bdzie zawiera pojedynczy element listy
            */}
            <Link className="showNavItem" to="/posts">
              {/*
                Link pozwala na nawigacj pomidzy stronami, do atrybutu to="/posts" przypisuj ciek do strony z postami. Link renderuje si jako znacznik a
              */}
              Posts
            </Link>
          </li>
          <li>
            {/*
              znacznik li bdzie zawiera pojedynczy element listy
            */}
            <Link className="showNavItem" to="/todos">
              {/*
                Link pozwala na nawigacj pomidzy stronami, do atrybutu to="/todos" przypisuj ciek do strony z zadaniami. Link renderuje si jako znacznik a
              */}
              Todos
            </Link>
          </li>
          <li>
            {/*
              znacznik li bdzie zawiera pojedynczy element listy
            */}
            <Link className="showNavItem" to="/users">
              {/*
                Link pozwala na nawigacj pomidzy stronami, do atrybutu to="/users" przypisuj ciek do strony z uytkownikami. Link renderuje si jako znacznik a
              */}
              Users
            </Link>
          </li>
        </ul>
      )}
    </nav>
  );
};

export default Navigation;
// eksportuje komponent Navigation
