import React, { useEffect, useState } from 'react';
// import hooka useEffect zeby mc go wykorzysta oraz useState
import axios from 'axios';
// import biblioteki axios do wykonywania metod HTTP
import ReactPaginate from 'react-paginate';
// import biblioteki ReactPaginate do wywietlania paginacji

interface Todo {
  id: number;
  title: string;
  due_on: string;
  status: string;
}

// tworze interfejs obiektu naszego API czyli szkielet tego jak ma wyglda obiekt

const Todos = () => {
  const [todos, setTodos] = useState<Todo[]>([]);
  // tworze state todos i ustawiam go na pust tablic na bazie interfejsu Todo czyli naszych danych pobieranych z API

  const [pageNumber, setPageNumber] = useState(0);
  // tworze state dla numeru strony (pocztkowo ustawiony na 0)
  const [actualPage, setActualPage] = useState(
    // tworze state dla aktualnej strony (pocztkowo ustawiony na 1)
    localStorage.getItem('todo') || 1
    // pobieram z localStorage aktualn stron lub ustawiam na 1
  );

  useEffect(() => {
    document.title = 'Todos';
    // ustawiam tytu strony na Todos

    axios
      .get(`https://gorest.co.in/public/v1/todos?page=${actualPage}`)
      // pobieram dane z API za pomoc biblioteki axios, metod (http) get i podaj adres API z numerem strony pobranym z localStorage (jeli nie ma to ustawiony jest na 1) dziki temu po odwieeniu strony nie zaczynamy od pocztku tylko od ostatniej strony na ktrej bylimy
      .then((response) => {
        // jeli dane zostay pobrane pomylnie to wykonuj ponisze instrukcje
        setPageNumber(response.data.meta.pagination.pages);
        //  pobieram z danych z API ilo wszystkich stron i ustawiam j jako ilo stron w paginacji
        setTodos(response.data.data);
        // ustawiam state todos na pobrane dane z API

        setActualPage(Number(localStorage.getItem('todo')));
        // ustawiam state actualPage na numer strony pobrany z localStorage
      })
      .catch((error) => {
        // jeli wystpi bd to wywietlam go w konsoli
        // console.log(error);
        // wywietlam bd w konsoli
      });
  }, [actualPage]);
  // wykonuj useEffect tylko wtedy gdy zmieni si state actualPage, spowoduje to ponowne pobranie danych z API i ponowne wywietlenie ich na stronie

  const changePage = ({ selected }: { selected: number }) => {
    // selected to numer strony na ktr klikniemy i jest typu number
    setActualPage(selected + 1);
    // ustawiam state actualPage na numer strony pobrany z obiektu selected i dodaj 1 poniewa numeracja stron w API zaczyna si od 1 a nie od 0

    localStorage.setItem('todo', JSON.stringify(selected + 1));
    // zapisuje do localStorage numer strony na ktrej jestem, kluczem jest todo, a wartoci numer strony
  };

  return (
    <>
      <h2 className="title">Todos</h2>
      {/* wywietlam tytu strony */}
      <ul className="wrapperParent" data-testid="todo">
        {/* tworze kontener dla listy */}
        {todos.map(({ id, title, due_on, status }: Todo) => {
          /*wywietlam list uytkownikw dziki metodzie tablicowej map poniewa jest to tablica obiektw w metodzie map przekazuj parametry id, name, email, gender, status za pomoc destrukturyzacji ktore bazuja na interfejsie Todo */
          return (
            <li className="item" key={id}>
              {/*
                wywietlam element listy dzieki znacznikowi li i dodaje mu klas item
                element listy ma key ktry jest rwny id
              */}
              <h2 className="itemTitle">{title}</h2>
              {/* wywietlam tytu */}
              <p className="itemInformation">{due_on}</p>
              {/* wywietlam dat */}
              <p className="itemInformation">{status}</p>
              {/* wywietlam status */}
            </li>
          );
        })}
      </ul>

      <ReactPaginate
        // wywietlam komponent ReactPaginate
        forcePage={
          // ustawiam aktualn stron na numer pobrany z localStorage
          localStorage.getItem('todo')
            ? // pobieram numer strony z localStorage
              Number(localStorage.getItem('todo')) - 1
            : // jeli localStorage nie jest pusty to przypisuje do state actualPage numer strony pobrany z localStorage
              0
        }
        // jesli znajdzie ustawia page -1 a jak nie znajdzie ustawia na 0
        previousLabel={'<'}
        // ustawiam etykiet dla przycisku poprzedniej strony
        nextLabel={'>'}
        // ustawiam etykiet dla przycisku nastpnej strony
        breakLabel={'...'}
        // ustawiam etykiet dla przycisku po rodku midzy stronami
        pageCount={pageNumber}
        // ustawiam ilo stron
        marginPagesDisplayed={1}
        // ustawiam ilo stron wywietlanych na kocu
        pageRangeDisplayed={1}
        // ustawiam ilo stron wywietlanych w rodku
        onPageChange={changePage}
        // ustawiam funkcj ktra ma si wykona po klikniciu na przycisk czyli changePage
        containerClassName="pagination"
        // ustawiam klas dla kontenera z przyciskami
        pageLinkClassName="page-num"
        // ustawiam klas dla przyciskw
        previousLinkClassName="page-num"
        // ustawiam klas dla przycisku poprzednia strona
        nextLinkClassName="page-num"
        // ustawiam klas dla przycisku nastpna strona
        activeClassName="active"
        // ustawiam klas dla aktywnego przycisku
      />
    </>
  );
};

export default Todos;
