import React, { useEffect, useState } from 'react';
// import hooka useEffect zeby mc go wykorzysta oraz useState
import axios from 'axios';
// import biblioteki axios do wykonywania metod HTTP
import ReactPaginate from 'react-paginate';
// import biblioteki ReactPaginate do wywietlania paginacji

interface Post {
  id: number;
  user_id: number;
  title: string;
  body: string;
}
// tworze interface obiektu naszego API czyli szkielet tego jak ma wyglda obiekt

interface Comment {
  id: number;
  post_id: number;
  body: string;
}
// tworze interface obiektu naszego API czyli szkielet tego jak ma wyglda obiekt

const Posts = () => {
  const [posts, setPosts] = useState<Post[]>([]);
  // tworze state posts i ustawiam go na pust tablic na bazie interfejsu Post czyli naszych danych pobieranych z API
  const [comments, setComments] = useState<Comment[]>([]);
  // tworze state comments i ustawiam go na pust tablic na bazie interfejsu Comment czyli naszych danych pobieranych z API

  const [title, setTitle] = useState('');
  // tworze state dla tytuu (pocztkowo pusty)
  const [body, setBody] = useState('');
  // tworze state dla treci (pocztkowo pusty)

  const [pageNumber, setPageNumber] = useState(0);
  // tworze state dla numeru strony (pocztkowo 0)
  const [actualPage, setActualPage] = useState(
    // tworze state dla aktualnej strony (pocztkowo 1)
    localStorage.getItem('post') || 1
    // pobieram z localStorage aktualn stron lub ustawiam na 1
  );

  useEffect(() => {
    document.title = 'Posts';
    // ustawiam tytu strony na Posts

    // axios
    //   .get(
    //     `https://gorest.co.in/public/v1/posts?page=${actualPage}`,
    //     // pobieram dane z API za pomoc biblioteki axios, metod (http) get i podaj adres API z numerem strony pobranym z localStorage (jeli nie ma to ustawiony jest na 1) dziki temu po odwieeniu strony nie zaczynamy od pocztku tylko od ostatniej strony na ktrej bylimy
    //     {
    //       headers: {
    //         // dodaj nagwki do zapytania
    //         Accept: 'application/json',
    //         // ustawiam typ danych na json
    //         'Content-Type': 'application/json',
    //         Authorization:
    //           'Bearer 1533f5328041264a1747ee4d995d1ffca1379aefb195c0fa7ee3f34b9d46b5f5',
    //         // ustawiam token autoryzacyjny ktry jest wymagany do pobrania danych z API poniewaz jest to API wykorzystujace wygenerowany token
    //       },
    //     }
    //   )
    //   .then((response) => {
    //     // jeli dane zostay pobrane pomylnie to wykonuj ponisze instrukcje
    //     setPageNumber(response.data.meta.pagination.pages);
    //     //  pobieram z danych z API ilo wszystkich stron i ustawiam j jako ilo stron w paginacji
    //     setPosts(response.data.data);
    //     // ustawiam state posts na pobrane dane z API
    //   })
    //   .catch((error) => {
    //     // jeli wystpi bd to wykonuj ponisze instrukcje
    //     // console.log(error);
    //     // wywietlam bd w konsoli
    //   });

    // axios
    //   .get(`https://gorest.co.in/public/v1/comments?page=${actualPage}`)
    //   // pobieram z API komentarze do postw
    //   .then((response) => {
    //     setComments(response.data.data);
    //     // ustawiam state comments na dane z API
    //   })
    //   .catch((error) => {
    //     // console.log(error);
    //   });

    fetch(`https://gorest.co.in/public/v1/posts?page=${actualPage}`, {
      // pobieram dane z API za pomoc biblioteki axios, metod (http) get i podaj adres API z numerem strony pobranym z localStorage (jeli nie ma to ustawiony jest na 1) dziki temu po odwieeniu strony nie zaczynamy od pocztku tylko od ostatniej strony na ktrej bylimy
      headers: {
        // dodaj nagwki do zapytania
        Accept: 'application/json',
        // ustawiam typ danych na json
        'Content-Type': 'application/json',
        Authorization:
          'Bearer 1533f5328041264a1747ee4d995d1ffca1379aefb195c0fa7ee3f34b9d46b5f5',
        // ustawiam token autoryzacyjny ktry jest wymagany do pobrania danych z API poniewaz jest to API wykorzystujace wygenerowany token
      },
    })
      .then((response) => response.json())
      .then((response) => {
        // jeli dane zostay pobrane pomylnie to wykonuj ponisze instrukcje
        setPageNumber(response.meta.pagination.pages);
        //  pobieram z danych z API ilo wszystkich stron i ustawiam j jako ilo stron w paginacji
        setPosts(response.data);
        // ustawiam state posts na pobrane dane z API
      })
      .catch((error) => {
        // jeli wystpi bd to wykonuj ponisze instrukcje
        // console.log(error);
        // wywietlam bd w konsoli
      });

    fetch(`https://gorest.co.in/public/v1/comments?page=${actualPage}`)
      // pobieram z API komentarze do postw
      .then((response) => response.json())
      .then((response) => {
        setComments(response.data);
        // ustawiam state comments na dane z API
      })
      .catch((error) => {
        // console.log(error);
      });
  }, [actualPage, title]);
  // wykonuj useEffect tylko wtedy gdy zmieni si state actualPage lub name, spowoduje to ponowne pobranie danych z API i ponowne wywietlenie ich na stronie

  const changePage = ({ selected }: { selected: number }) => {
    // selected to numer strony na ktr klikniemy i jest typu number
    // tworze funkcje changePage ktra przyjmuje obiekt selected typu number
    setActualPage(selected + 1);
    // ustawiam state actualPage na numer strony pobrany z obiektu selected i dodaj 1 poniewa numeracja stron w API zaczyna si od 1 a nie od 0

    localStorage.setItem('post', JSON.stringify(selected + 1));
    // zapisuje do localStorage numer strony na ktrej jestem, kluczem jest post, a wartoci numer strony
  };

  const createPost = () => {
    if (title === '' || body === '') {
      // jeli title lub body s puste to wykonuj ponisze instrukcje
      return alert('Please fill all fields');
      // wywietlam komunikat o bdzie i kocz dziaanie funkcji
    }
    axios
      .post(
        'https://gorest.co.in/public/v1/posts',
        {
          // wysyam dane do API za pomoc biblioteki axios, metod (http) post i podaj adres API
          title: title,
          // przypisuje do pola title warto pobran z state title
          body: body,
          // przypisuje do pola body warto pobran z state body
          user_id: Math.floor(Math.random() * 1000),
          // przypisuje do pola user_id identyfikator uytkownika, wykorzystuj do tego metode Math.random ktra zwraca losow liczb z przedziau 0-1, mno j przez 1000 dziki czemu otrzymuj liczb z przedziau 0-1000, nastpnie zaokrglam j do najbliszej liczby cakowitej za pomoc metody Math.floor
        },
        {
          headers: {
            // dodaj nagwki do zapytania
            Accept: 'application/json',
            // ustawiam typ danych na json
            'Content-Type': 'application/json',

            Authorization:
              'Bearer 1533f5328041264a1747ee4d995d1ffca1379aefb195c0fa7ee3f34b9d46b5f5',
            // ustawiam token autoryzacyjny ktry jest wymagany do pobrania danych z API poniewaz jest to API wykorzystujace wygenerowany token
          },
        }
      )
      .then((response) => {
        // jeli dane zostay wysane pomylnie to wykonuj ponisze instrukcje
        setTitle('');
        // ustawiam state title na pusty string poniewa po wysaniu posta chc eby pole title byo puste
        setBody('');
        // ustawiam state body na pusty string poniewa po wysaniu posta chc eby pole body byo puste
      })
      .catch((error) => {
        // jeli wystpi bd to wykonuj ponisze instrukcje
        // console.log(error);
        // wywietlam bd w konsoli
      });
  };

  return (
    <>
      <h2 className="title">Posts</h2>
      {/* 
      // wywietlam tytu strony
      */}

      <div className="wrapperForm">
        {/* 
        // tworz kontener dla formularza
        */}
        <input
          className="information"
          required
          type="text"
          placeholder="Post"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        {/* 
          wyswietlam pole do wpisania posta dzieki znacznikowi input i dodaje mu klase information
          ustawiam pole jako wymagane (required)
          ustawiam typ pola na text
          ustawiam placeholder na Post
          ustawiam value na state title
          pole ma event onChange ktry wywouje funkcj setPost ktra ustawia state name na warto wpisan w pole po kadej zmianie
        */}
        <input
          className="information"
          required
          type="text"
          placeholder="Body"
          value={body}
          onChange={(e) => setBody(e.target.value)}
        />
        {/* 
          wyswietlam pole do wpisania posta dzieki znacznikowi input i dodaje mu klase information
          ustawiam pole jako wymagane (required)
          ustawiam typ pola na text
          ustawiam placeholder na Body
          ustawiam value na state body
          pole ma event onChange ktry wywouje funkcj setBody ktra ustawia state body na warto wpisan w pole po kadej zmianie
        */}

        <button className="submit" onClick={createPost}>
          Create Post
        </button>
        {/* 
        tworze przycisk ktry po klikniciu wywouje funkcj createPost
        */}
      </div>
      <ul className="wrapperParent" data-testid="post">
        {posts.map(({ id, title, body, user_id }: Post) => (
          // wywietlam posty za pomoc metody tablicowej map poniewaz jest to tablica obiektw, w metodzie map przekazuje parametry id, title, body, user_id, za pomoc destrukturyzacji ktore
          // bazuja na interfejsie Post
          <li
            className="item"
            // wywietlam posty w licie
            key={id}
            // ustawiam klucz na id posta
          >
            <h2 className="itemTitle">{title}</h2>
            {/* 
            wywietlam tytu postu
            */}
            <p className="itemInformation">{body}</p>
            {/* 
            wywietlam tre postu
            */}
            <p className="itemInformation">{id}</p>

            {/* 
            wywietlam id postu
            */}

            <p className="itemInformation">{user_id}</p>

            {/* 
            wywietlam id uytkownika
            */}

            {comments
              .filter(({ post_id }: Comment) => post_id === id)
              .map(({ id, body }: Comment) => (
                // sprawdzam czy id postu jest rwne id komentarza za pomoc metody tablicowej filter i wywietlam komentarze do postu za pomoc metody tablicowej map
                <p key={id} className="itemInformation">
                  {body}
                </p>
                // wywietlam komentarze do postu i przypisuj im key id komentarza zeby nie byo bdu w konsoli
              ))}
          </li>
        ))}
      </ul>

      <ReactPaginate
        // wywietlam komponent ReactPaginate
        forcePage={
          // ustawiam stron na ktr ma by przekierowany uytkownik po klikniciu w przycisk
          localStorage.getItem('post')
            ? // pobieram z localStorage warto klucza post
              Number(localStorage.getItem('post')) - 1
            : // jeli localStorage nie jest pusty to przypisuj do forcePage warto klucza post - 1 poniewa numeracja stron w ReactPaginate zaczyna si od 0
              0
          // jeli localStorage jest pusty to przypisuj do forcePage warto 0
        }
        previousLabel={'<'}
        // ustawiam etykiet dla przycisku poprzedniej strony
        nextLabel={'>'}
        // ustawiam etykiet dla przycisku nastpnej strony
        breakLabel={'...'}
        // ustawiam etykiet dla przycisku po rodku midzy stronami
        pageCount={pageNumber}
        // ustawiam ilo stron
        marginPagesDisplayed={1}
        // ustawiam ilo stron wywietlanych na kocu
        pageRangeDisplayed={1}
        // ustawiam ilo stron wywietlanych w rodku
        onPageChange={changePage}
        // ustawiam funkcj ktra ma si wykona po klikniciu na przycisk czyli changePage
        containerClassName="pagination"
        // ustawiam klas dla kontenera z przyciskami
        pageLinkClassName="page-num"
        // ustawiam klas dla przyciskw
        previousLinkClassName="page-num"
        // ustawiam klas dla przycisku poprzednia strona
        nextLinkClassName="page-num"
        // ustawiam klas dla przycisku nastpna strona
        activeClassName="active"
        // ustawiam klas dla aktywnego przycisku
      />
    </>
  );
};

export default Posts;
// eksportuj komponent Posts
