import React, { useEffect } from 'react';
// importuje hooka useEffect eby mc go wykorzysta

const ErrorPage = () => {
  useEffect(() => {
    // useEffect jest odpowiedzialny za wykonanie funkcji po zaadowaniu strony
    document.title = 'Error';
    // ustawiam tytu strony na Error
  }, []);
  // [] - tablica zalenoci, jeli tablica jest pusta to funkcja useEffect wykona si tylko raz po zaadowaniu strony

  return <h2 className="title">Page not found</h2>;
  // wywietlam tytu strony
};

export default ErrorPage;
// eksportujemy komponent ErrorPage
