import React from 'react';
import './App.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Navigation from './components/Navigation';
import Home from './pages/Home';
import Posts from './pages/Posts';
import Todos from './pages/Todos';
import Users from './pages/Users';
import ErrorPage from './pages/ErrorPage';
import './styles/home.scss';
import './styles/navigation.scss';
import './styles/apiPages.scss';

function App() {
  // komponent App ktry bdzie zawiera wszystkie inne komponenty
  return (
    // zwracam komponent Router ktry jest odpowiedzialny za nawigacj pomidzy stronami
    <Router>
      {/* 
        Router pozwala nam na owinicie wszystkich komponentw, dziki temu bdziemy mogli nawigowa si pomidzy stronami
      */}
      <div className="App">
        {/* 
          div zawiera ca aplikacj 
        */}
        <Navigation />
        {/* 
          wywietlam komponent Navigation ktry jest odpowiedzialny za nawigacj pomidzy stronami
        */}
        <Routes>
          {/* 
            Routes zawiera wszystkie routy do innych stron 
          */}
          <Route path="/" element={<Home />} />
          {/* 
            Route jest odpowiedzialny za wywietlanie odpowiedniej strony w zalenoci od cieki path, do path przypisuj ciek do strony gwnej, do element przypisuj komponent Home ktry jest odpowiedzialny za wywietlanie strony gwnej
          */}

          <Route path="/posts" element={<Posts />} />
          <Route path="/todos" element={<Todos />} />
          <Route path="/users" element={<Users />} />
          <Route path="*" element={<ErrorPage />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
