import React, { useEffect, useState } from 'react';
// import hooka useEffect ktry jest odpowiedzialny za wykonanie funkcji po zaadowaniu strony oraz useState ktry jest odpowiedzialny za tworzenie i aktualizowanie stanu
import axios from 'axios';
// importuje bibliotek axios ktra jest odpowiedzialna za komunikacj z API
import ReactPaginate from 'react-paginate';
// import biblioteki ReactPaginate ktra jest odpowiedzialny za paginacj

interface User {
  email: string;
  gender: string;
  id: number;
  name: string;
  status: string;
}

// tworze interface obiektu naszego API czyli szkielet tego jak ma wyglda obiekt

const Users = () => {
  const [users, setUsers] = useState<User[]>([]);
  // tworze state users i ustawiam go na pust tablic na bazie interfejsu User czyli naszych danych pobieranych z API

  const [name, setName] = useState('');
  // tworze state dla imienia (pocztkowo pusty)

  const [email, setEmail] = useState('');
  // tworze state dla emaila (pocztkowo pusty)
  const [gender, setGender] = useState('male');
  // tworze state dla pci (pocztkowo ustawiony na male)
  const [status, setStatus] = useState('active');
  // tworze state dla statusu (pocztkowo ustawiony na active)

  const [pageNumber, setPageNumber] = useState(0);
  // tworze state dla numeru strony (pocztkowo ustawiony na 0)
  const [actualPage, setActualPage] = useState(
    // tworze state dla aktualnej strony (pocztkowo ustawiony na 1)

    localStorage.getItem('page') || 1
    // pobieram z localStorage aktualn stron lub ustawiam na 1
  );

  useEffect(() => {
    document.title = 'Users';
    // ustawiam tytu strony na Users

    axios
      .get(`https://gorest.co.in/public/v1/users?page=${actualPage}`, {
        // pobieram dane z API za pomoc biblioteki axios, metod (http) get i podaj adres API z numerem strony pobranym z localStorage (jeli nie ma to ustawiony jest na 1) dziki temu po odwieeniu strony nie zaczynamy od pocztku tylko od ostatniej strony na ktrej bylimy
        headers: {
          // dodaj nagwki do zapytania

          Accept: 'application/json',
          // ustawiam typ danych na json
          'Content-Type': 'application/json',
          // ustawiam typ danych na json

          Authorization:
            'Bearer 1533f5328041264a1747ee4d995d1ffca1379aefb195c0fa7ee3f34b9d46b5f5',
          // ustawiam token autoryzacyjny ktry jest wymagany do pobrania danych z API poniewaz jest to API wykorzystujace wygenerowany token
        },
      })
      .then((response) => {
        // jeli dane zostay pobrane pomylnie to wykonuj ponisze instrukcje
        // pobieram z danych z API ilo wszystkich elementw
        setPageNumber(response.data.meta.pagination.pages);
        //  pobieram z danych z API ilo wszystkich stron i ustawiam j jako ilo stron w paginacji

        setUsers(response.data.data);
        // ustawiam state users na pobrane dane z API

        setActualPage(Number(localStorage.getItem('page')));
        // ustawiam state actualPage na numer strony pobrany z localStorage
      })
      .catch((error) => {
        // jeli wystpi bd to wykonuj ponisze instrukcje
        // console.log(error);
        // wywietlam bd w konsoli
      });
  }, [actualPage, name]);
  // wykonuj useEffect tylko wtedy gdy zmieni si state actualPage lub name, spowoduje to ponowne pobranie danych z API i ponowne wywietlenie ich na stronie

  const changePage = ({ selected }: { selected: number }) => {
    // selected to numer strony na ktr klikniemy i jest typu number

    setActualPage(selected + 1);
    // ustawiam state actualPage na numer strony pobrany z obiektu selected i dodaj 1 poniewa numeracja stron w API zaczyna si od 1 a nie od 0

    localStorage.setItem('page', JSON.stringify(selected + 1));
    // zapisuje do localStorage numer strony na ktrej jestem, kluczem jest post, a wartoci numer strony
  };

  const createUser = () => {
    // tworze funkcj createUser

    if (name === '' || email === '') {
      // jeli pole name lub email jest puste to
      return alert('Please fill all the fields');
      // zwracam alert z komunikatem o bdzie i kocz dziaanie funkcji
    } else if (
      !email.includes('@') ||
      !email.includes('.') ||
      email.length < 5
      // jeli pole email nie zawiera @ lub . lub jest krtsze ni 5 znakw to
    ) {
      return alert('Please enter a valid email');
      // zwracam alert z komunikatem o bdzie i kocz dziaanie funkcji
    }
    axios
      .post(
        'https://gorest.co.in/public/v1/users',
        {
          // wysyam dane do API za pomoc biblioteki axios, metod (http) post i podaj adres API

          name: name,
          // przypisuje do pola name warto pobran z state name
          email: email,
          // przypisuje do pola email warto pobran z state email
          gender: gender,
          // przypisuje do pola gender warto pobanan z state gender
          status: status,
          // przypisuje do pola status warto pobran z state status
        },
        {
          headers: {
            // dodaj nagwki do zapytania
            Accept: 'application/json',
            // ustawiam typ danych na json
            'Content-Type': 'application/json',
            // ustawiam typ danych na json
            Authorization:
              'Bearer 1533f5328041264a1747ee4d995d1ffca1379aefb195c0fa7ee3f34b9d46b5f5',
            // ustawiam token autoryzacyjny ktry jest wymagany do pobrania danych z API poniewaz jest to API wykorzystujace wygenerowany token
          },
        }
      )
      .then((response) => {
        // jeli dane zostay wysane pomylnie to wykonuj ponisze instrukcje
        setName('');
        // ustawiam state name na pusty string poniewaz po wysaniu danych chc aby pole name byo puste
        setEmail('');
        // ustawiam state email na pusty string poniewaz po wysaniu danych chc aby pole name byo puste
        setGender('male');
        // ustawiam state gender na pusty string poniewaz po wysaniu danych chc aby pole name byo puste
        setStatus('active');
        // ustawiam state status na pusty string poniewaz po wysaniu danych chc aby pole name byo puste
      })
      .catch((error) => {
        // jeli wystpi bd to wykonuj ponisze instrukcje
        // console.log(error);
        // wywietlam bd w konsoli
      });
  };

  return (
    <>
      <h2 className="title">Users</h2>
      {/* 
        wywietlam tytu strony dzieki znacznikowi h1 i dodaje mu klas title
      */}
      <div className="wrapperForm">
        {/* 
         tworz kontener dla formularza
        */}
        <input
          className="information"
          required
          type="text"
          placeholder="Enter name"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
        {/* 
          wywietlam pole do wpisania imienia dzieki znacznikowi input i dodaje mu klas information
          pole jest wymagane (required)
          pole jest typu text
          pole ma placeholder o treci Enter name
          pole ma warto pobran z state name
          pole ma event onChange ktry wywouje funkcj setName ktra ustawia state name na warto wpisan w pole po kadej zmianie
        */}
        <input
          className="information"
          required
          type="email"
          placeholder="Enter email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        {/*
          wywietlam pole do wpisania emaila dzieki znacznikowi input i dodaje mu klas information
          pole jest wymagane (required)
          pole jest typu email
          pole ma placeholder o treci Enter email
          pole ma warto pobran z state email
          pole ma event onChange ktry wywouje funkcj setEmail ktra ustawia state name na warto wpisan w pole po kadej zmianie
        */}
        <select
          className="information"
          required
          value={gender}
          onChange={(e) => {
            const selectedGender = e.target.value;
            setGender(selectedGender);
          }}
        >
          {/* 
              wywietlam pole do wyboru pci dzieki znacznikowi select i dodaje mu klas information
              pole jest wymagane (required)
              pole ma warto pobran
              pole ma event onChange ktry wywouje funkcj setGender  ktra ustawia 

            */}

          <option value="male">male</option>
          {/* 
            pierwsza opcja ma wartosc male

          */}
          <option value="female">female</option>
          {/* 
            druga opcja ma wartosc female
          */}
        </select>
        <select
          className="information"
          required
          value={status}
          onChange={(e) => setStatus(e.target.value)}
        >
          <option value="active">Active</option>
          {/* 
            pierwsza opcja ma wartosc active
          */}
          <option value="inactive">Inactive</option>
          {/* 
            druga opcja ma wartosc inactive
          */}
        </select>

        <button className="submit" onClick={createUser}>
          {/* 
        tworze przycisk ktry po klikniciu wywouje funkcj createUser
        */}
          create user
        </button>
        {/*
          wywietlam przycisk dzieki znacznikowi button i dodaje mu klas submit
          przycisk ma event onClick ktry wywouje funkcj createUser
        */}
      </div>

      <ul className="wrapperParent" data-testid="user">
        {/* 
          tworz kontener dla listy
        */}
        {users.map(({ id, name, email, gender, status }: User) => {
          /*wywietlam list uytkownikw dziki metodzie tablicowej map poniewa jest to tablica obiektw w metodzie map przekazuj parametry id, name, email, gender, status za pomoc destrukturyzacji ktore bazuja na interfejsie User */
          return (
            <li className="item" key={id}>
              {/*
                wywietlam element listy dzieki znacznikowi li i dodaje mu klas item
                element listy ma key ktry jest rwny id
              */}
              <h2 className="itemTitle">{name}</h2>
              {/* 
                wywietlam imi uytkownika 
              */}
              <p className="itemInformation">{email}</p>
              {/*
                wywietlam email uytkownika 
              */}
              <p className="itemInformation">{gender}</p>
              {/*
                wywietlam pe uytkownika
              */}
              <p className="itemInformation">{status}</p>
              {/*
                wywietlam status uytkownika
              */}
            </li>
          );
        })}
      </ul>

      <ReactPaginate
        // wywietlam komponent ReactPaginate
        forcePage={
          // ustawiam aktualn stron na numer pobrany z localStorage
          localStorage.getItem('page')
            ? // pobieram numer strony z localStorage
              Number(localStorage.getItem('page')) - 1
            : // jeli localStorage nie jest pusty to przypisuje do state actualPage numer strony pobrany z localStorage
              0
          // jeli localStorage jest pusty to przypisuje do state actualPage 0
        }
        // jesli znajdzie ustawia page -1 a jak nie znajdzie ustawia na 0
        previousLabel={'<'}
        // ustawiam etykiet dla przycisku poprzedniej strony
        nextLabel={'>'}
        // ustawiam etykiet dla przycisku nastpnej strony
        breakLabel={'...'}
        // ustawiam etykiet dla przycisku po rodku midzy stronami
        pageCount={pageNumber}
        // ustawiam ilo stron
        marginPagesDisplayed={1}
        // ustawiam ilo stron wywietlanych na kocu
        pageRangeDisplayed={1}
        // ustawiam ilo stron wywietlanych w rodku
        onPageChange={changePage}
        // ustawiam funkcj ktra ma si wykona po klikniciu na przycisk czyli changePage
        containerClassName="pagination"
        // ustawiam klas dla kontenera z przyciskami
        pageLinkClassName="page-num"
        // ustawiam klas dla przyciskw
        previousLinkClassName="page-num"
        // ustawiam klas dla przycisku poprzednia strona
        nextLinkClassName="page-num"
        // ustawiam klas dla przycisku nastpna strona
        activeClassName="active"
        // ustawiam klas dla aktywnego przycisku
      />
    </>
  );
};

export default Users;
// eksportuj komponent Users
